/* DOM Helpers */
html, body { height: 100%; min-width: 320px;background:#311b1f;}
body { margin: 0px auto; -webkit-text-size-adjust: none; min-width: 320px; margin: 0 auto; font-family: "Open Sans", sans-serif; line-height: 125%; color: #252525; text-align: center;}
body * { line-height: 125%; text-size-adjust: none; text-size-adjust: auto;text-size-adjust: 80%;  }

body div { box-sizing: border-box; }

hr{height:1px;background-color: #fff; opacity: 0.75;border:0px none;margin:20px 0;transform: translateZ(0);will-change:transform;}

.clear { clear: both; }
.rgt { float:right; }
.lft { float:left;}

.no-caps{text-transform: none;}

.sep{ color: #ff617f;}
.mobile-show, 
.tablet-show{display:none;}
/* END DOM Helpers */



/* LINKS & Call-To-Actions */
a { color: #ff617f; text-decoration: none;border-bottom: 1px dotted #fff; padding-bottom:2px;  transition: all 0.25s; }
a:hover { color: #fff; border-color: #ff617f; transition: all 0.25s; }
a.alt { color: #fff; border-bottom: 1px dotted #fff;  }
a.alt:hover { color: #fff; border-color: #ff617f; }

.btn { cursor: pointer; transition: border-color 0.35s 0.25s,background-color 0.35s 0.25s, color 0.35s 0.25s; font-size: 0.75rem; padding: 12px 15px; font-family: "Work Sans", sans-serif; display: inline-block; background-color: #ff617f; position: relative; min-width: 120px; color: #fff; text-decoration: none; font-weight: 400; text-align: center; border:1px solid #ff617f;text-transform: uppercase;text-shadow: none;border-radius: 5px; letter-spacing: 2px;}
.btn:hover { background-color: transparent;color:#ff617f; transition: background-color 0.35s 0.25s, color 0.35s 0.25s; }
.btn.alt { color: #ff617f;border:1px solid #ff617f;}
.btn.alt:hover {color: #fff; }
.btn.hvLight:hover {color:#fff;border-color:#fff; transition: border-color 0.35s 0.25s,  background-color 0.35s 0.25s, color 0.35s 0.25s;}
.btn.disabled{background:#ddd;border-color:#ddd;color:#aaa;cursor:not-allowed;transition: border-color 0.35s 0.25s,background-color 0.35s 0.25s, color 0.35s 0.25s;}

.btn .icon{ vertical-align:baseline; margin-left:5px;display: inline-block;position: relative;width: 8px;}
.btn .icon img{display:block;max-width: 100%;transition: 0.35s 0.25s;transform: translateZ(0);will-change:transform;}
.btn .icon img.hover{position: absolute;top:0; opacity: 0; transition: 0.35s 0.25s;transform: translateZ(0);will-change:transform;}
.btn:hover .icon img{opacity: 0; transition: opacity 0.35s 0.25s;transform: translateZ(0);will-change:transform;}
.btn:hover .icon img.hover{opacity: 1;transition: opacity 0.35s 0.25s;transform: translateZ(0);will-change:transform;}
.btn.hvLight:hover .icon img.hover{opacity: 0;transform: translateZ(0);will-change:transform;}
.btn.hvLight:hover .icon img{opacity: 1;transform: translateZ(0);will-change:transform;}
/* END LINKS & Call-To-Actions */



/* Body Text */
h1, h2, h3, h4, h5, h6 { font-family: "Work Sans", sans-serif; color: #252525; line-height: 125%; }

h1 { font-size: 3rem; margin: 20px 0; font-weight: 500; text-transform:uppercase; letter-spacing: 5px; }
h2 { font-size: 2rem; margin: 10px 0;text-transform:uppercase; letter-spacing: 2px;  }
h3 { font-size: 1.25rem; margin: 10px 0; text-transform:uppercase; letter-spacing: 2px;}
h4 { font-size: 1rem; margin: 10px 0; text-transform:uppercase; letter-spacing: 2px;}
h5 { font-size: 0.85rem; margin: 10px 0; text-transform:uppercase;}
h6 { font-size: 0.7rem; margin: 10px 0;text-transform:uppercase; }

p { margin:20px 0; line-height: 150%;}
h1 + p { margin-top: -15px; }
p + img { margin-top:-15px;}

ul, ol { margin: 0px 0 0px 0px; padding: 0; }
ul li, ol li { margin: 0px 0 0 0px; padding: 0px 0 0 0px; line-height: 120%; }
/*.mid ul { padding: 10px 40px; list-style-type: circle; }
.mid ul li { padding: 3px 6px; }
.mid ul ul { padding: 4px 40px; }*/
/* END Body Text */

/* Overlays */
.overlay{background: linear-gradient(180deg, rgba(25,25,25,1) 0%, rgba(0,0,0,0) 100%)  no-repeat  center top; background-size: cover; background-color: rgba(0,0,0,0.2); position:absolute; left:0;top:0;right:0;bottom:0; z-index: 2;}
.overlay.pattern{background: transparent repeat; background-size: auto;opacity: 0.25;transform: translateZ(0);will-change:transform;}
.overlay.pattern.type1{background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAECAYAAACp8Z5+AAAAGUlEQVQImWNgYGD4D8VwgCGAU+I/AwPDfwADTwf58XOFKQAAAABJRU5ErkJggg==);}
.overlay.pattern.type2{background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAECAYAAACp8Z5+AAAAEUlEQVQImWNgwAH+o9F4VAAAV+wD/YVTOGcAAAAASUVORK5CYII=); }
.overlay.flat{background-image:none; background-color: rgba(0,0,0,0.5);  }
.full-overlay{top:0%;bottom:0;left:0;right:0; height:0%;background:#fff;z-index:1;}
.full-overlay.baseApp{position:fixed;z-index:0;height:100%;background:#311b1f repeat center top ; background-size: auto;transition:opacity 0.001s 5s; opacity:1;}
.full-overlay.baseApp.waitInit{opacity:0;transition:none;}

/* END Overlays */


/* misc */
.transitGrp > div{background:#fff;}


@media screen and (max-width: 1440px) {
    h2{font-size: 1.75rem;}
}

@media screen and (max-width: 960px) {
    .tablet-hide{display:none;}
    .tablet-show{display:block;}
}

@media screen and (max-width: 769px) {
    html, body{font-size: 12px;}
    h2 {font-size:2.25rem;}
    .btn{font-size:1rem;padding:12px 10px; min-width: auto;}
    .mobile-hide{display:none;}
    .mobile-show{display:block;}
    /* body [data-aos][data-aos]{transform:none ;opacity: 1 ;transform: translateZ(0);will-change:transform;opacity: unset;} */
}

@media screen and (max-width: 512px) {
    .btn{font-size:0.65rem;padding:10px 8px;}
}